.features__title {
  margin: 0;
  padding: 0;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  margin-bottom: 38px;

  @media (min-width: $tablet) {
    font-size: 60px;
    line-height: 60px;
  }

  @media (min-width: $desktop) {
    margin-bottom: 32px;
  }
}

.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  min-height: 300px;
  padding-bottom: 42px;
  counter-reset: number;

  @media (min-width: $tablet) {
    flex-direction: row;
    justify-content: space-between;
    padding-right: 79px;
    padding-bottom: 0;
  }

  @media (min-width: $desktop) {
    justify-content: space-between;
    padding-right: 0;
  }
}

.features__item {
  margin: 0;
  padding: 0;
  padding-left: 80px;
  position: relative;

  @media (min-width: $tablet) {
    max-width: 250px;
    height: 280px;
    padding-left: 0;
    padding-bottom: 57px;
    display: flex;
    align-items: flex-end;

    &::after {
      counter-increment: number;
      content: counter(number);
      position: absolute;
      font-size: 280px;
      line-height: 280px;
      top: -16px;
      right: 0;
      color: var(--basic-gray);
      z-index: -1;
    }
  }
}

.features__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  padding: 0;
  font-weight: 400;

  @media (min-width: $tablet) {
    font-size: 16px;
    line-height: 24px;
  }
}
